@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap');
:root {
    --font-family: "Montserrat";
    --dark-blue: #0a1930;
    --light-blue: #1f93ff;
  
    --color-white: #fff;
    --color-dark: #333;
  
    --color-grey: #eee;
    --box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  
    --color-purple: #9d0191;
    --color-orange: #ff7722;
  
    --color-primary: #007bff;
    --color-success: #28a745;
    --color-danger: orangered;
  }
  
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
  }
  
  html {
    font-size: 10px;
    background-color: #282A3A;
  }
  
  body {
    font-family: var(--font-family);
  }
  
  section {
    width: 100%;
    padding: 8rem 0;
  }
  
  .container {
    max-width: 1000px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  /* UTILITY CLASSES */
  
  /* Flex */
  .--flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .--flex-start {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
  }
  .--flex-end {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  .--flex-between {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .--dir-column {
    flex-direction: column;
  }
  
  .--flex-dir-column {
    display: flex;
  }
  
  .--align-center {
    display: flex;
    align-items: center;
  }
  
  .--100vh {
    height: 100vh;
  }
  
  .--mh-100vh {
    min-height: 100vh;
  }
  
  
  /* Center All */
  .--center-all {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    margin: auto;
    text-align: center;
  }

  .--border-rounded{
    border-radius: 2px;
  }

  .overview{
    position: relative;
    top: 80px;
    font-family: 'Montserrat';
  }
  
  /* Heading */
  h1,
  h2,
  h3,
  h4 {
    font-family: var(--font-family);
    font-weight: 500;
    line-height: 1.2;
    color: var(--color-dark);
    margin-bottom: 1rem;
  }
  h1 {
    font-size: 6rem;
    text-align: center;
    color: white;
  }

  h2 {
    font-size: 3rem;
  }
  h3 {
    font-size: 2.5rem;
  }
  h4 {
    font-size: 2rem;
    color: black;
    font-weight: 600;
  }
  
  p {
    font-size: 2.5rem;
    /* font-weight: 300; */
    /* line-height: 1.3; */
    color: var(--color-dark);
  }

  .oops{
    position: relative;
    top: 100px;
    text-align: center;
    font-weight: 500;
    font-size: 50px;
  }

  .oops-link{
    position: relative;
    top: 100px;
    
  }

.--spacing-text{
    letter-spacing: 0.07rem;
}
 .--text-underline{
    text-decoration: underline;
  text-decoration-color: black;
  text-decoration-thickness: 4px;
  text-underline-offset: 6px;
  }
  .--text-xl {
    font-size: 4.5rem;
  }
  .--text-lg {
    font-size: 4rem;
  }
  
  .--text-md {
    font-size: 3rem;
  }
  
  .--text-sm {
    font-size: 1.2rem;
    font-weight: 300;
  }
  
  .--text-p {
    font-size: 1.5rem;
    font-weight: 300;
    line-height: 1.3;
    color: var(--color-dark);
  }
  
  .--fw-bold {
    font-weight: 800;
  }

  .--fw-med{
    font-weight: 600;
  }

  .--fw-thin {
    font-weight: 200;
  }
  
  /* Text Color */
  .--text-light {
    color: #fff;
  }
  
  .--color-primary {
    color: #007bff;
  }
  .--color-danger {
    color: orangered;
  }
  .--color-success {
    color: #28a745;
  }
  
  .--color-white {
    color: #fff;
  }
  
  /* Center Text */
  .--text-center {
    text-align: center;
  }

  .--text-left {
    text-align:left;
  }

  .card-contact{
    position: relative;
    top: 20px;
    border: rgb(92, 92, 92) solid;
    border-radius: 5px;
    border-width: 0.8px;
    padding-top: 12px;
  }
  
  /* Card */
  .--card {
    border: 1px solid transparent;
    border-radius: 5px;
    box-shadow: var(--box-shadow);
    /* overflow: hidden; */
    /* width: 50%; */
    max-width: 25%;
    background-color: rgb(255, 255, 255);
  }

  .--img{
    border-radius: 100%;
    width: 19rem;
    padding: 20px;

  }

  .--pos{
    position: relative;
    top: 100px;
  }
  
  /* Margin */
  .--m {
    margin: 1rem;
  }
  .--ml {
    margin-left: 1rem;
  }
  .--mr {
    margin-right: 1rem;
  }
  
  .--mb {
    margin-bottom: 1rem;
  }
  
  .--my {
    margin: 1rem 0;
  }
  .--mx {
    margin: 0 1rem;
  }
  
  .--m2 {
    margin: 2rem;
  }
  
  .--ml2 {
    margin-left: 2rem;
  }
  .--mr2 {
    margin-right: 2rem;
  }
  
  .--mb2 {
    margin-bottom: 2rem;
  }
  
  .--my2 {
    margin: 2rem 0;
  }
  
  .--mx2 {
    margin: 0 2rem;
  }
  
  /* Padding */
  .--p {
    padding: 1rem;
  }
  .--p2 {
    padding: 2rem;
  }
  .--py {
    padding: 1rem 0;
  }
  .--py2 {
    padding: 2rem 0;
  }
  .--px {
    padding: 0 1rem;
  }
  .--px2 {
    padding: 0 2rem;
  }
  
  .--btn {
    font-size: 1.6rem;
    font-weight: 400;
    padding: 6px 8px;
    margin: 0 5px 0 0;
    border: 1px solid transparent;
    border-radius: 3px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.3s;
  }
  
  .--btn:hover {
    transform: translateY(-2px);
  }
  
  .--btn-lg {
    padding: 8px 10px;
  }

  .--btn-sm {
    font-size: 1.2rem;
    font-weight: 500;
    padding: 6px 8px;
    margin: 0 5px;
    padding: 4px 5px;
  }
  
  .--details{
    font-size: 1.6rem;
    font-weight: 400;
    list-style-type: null;
  }
  .dashboard{
    position: relative;
    top: 60px;
  }
.helloUser{
  position: relative;
  top: 100px;
  margin-left: 20px;
  /* left: 20px; */
  /* letter-spacing: 0.1rem; */
  font-size: 36px;
}
  .buttons{
    position: relative;
    top: 60px;
  }

  .btn{
    width: 120px;
    padding: 1 1;
    margin: 5 6px;
  }

  .btn:hover{
    background-color: white;
    color: black;
    border-color: black;
  }
  .--btn-block {
    width: 100%;
  }
  
  .--btn-primary {
    color: #fff;
    background: #007bff;
  }
  .--btn-secondary {
    color: #fff;
    border: 1px solid #fff;
    background: transparent;
  }
  .--btn-danger {
    color: #fff;
    background: orangered;
  }
  
  .--btn-success {
    color: #fff;
    background: #28a745;
  }
  
  /* Background */
  .--bg-light {
    background: #fff;
  }
  .--bg-dark {
    background: var(--color-dark);
  }
  .--bg-primary {
    background: var(--color-primary);
  }
  .--bg-success {
    background: var(--color-success);
  }
  .--bg-grey {
    background: var(--color-grey);
  }
  
  .--form-control {
    font-size: 1.6rem;
    font-weight: 300;
  }
  
  .--form-control > * {
    margin: 5px 0;
  }
  
  .--form-control input {
    font-size: 1.6rem;
    font-weight: 300;
    padding: 8px 1rem;
    border: 1px solid #777;
    border-radius: 3px;
    outline: none;
  }
  .--form-control select {
    font-size: 1.4rem;
    font-weight: 400;
    padding: 8px 1rem;
    border: 1px solid #777;
    border-radius: 3px;
  }
  
  .--form-control label {
    font-size: 1.6rem;
    font-weight: 400;
    display: inline-block;
    min-width: 7rem;
    color: var(--color-dark);
    margin-right: 1rem;
  }
  
  @media screen and (max-width: 600px) {
    .--flex-dir-column {
      flex-direction: column;
    }
  }
  
  .--block {
    display: block;
  }
  .--inline-block {
    display: inline-block;
  }
  
  .--width-100 {
    width: 100%;
  }
  .--width-50{
    width: 50%;
  }
  
  .--width-500px {
    width: 500px;
  }
  
  .--line {
    position: relative;
  }
  .--line::after {
    content: "";
    position: absolute;
    left: 50%;
    bottom: -5px;
    transform: translateX(-50%);
    width: 5rem;
    height: 3px;
    margin-bottom: 1rem;
  
    background: rgb(26, 17, 23);
    background: linear-gradient(
      135deg,
      rgb(39, 25, 42) 44%,
      rgb(45, 36, 43) 57%
    );
  }
  
  .--list-style-none {
    list-style: none;
  }
  
  .--profile-img {
    width: 6rem;
    height: 6rem;
    border: 1px solid #ccc;
    border-radius: 50%;
  }
  
  a {
    font-size: 1.4rem;
    color: var(--dark-blue);
    text-decoration: none;
    transition: all 0.2s;
  }


.--logos{
  padding:10px;
  margin: 10px;
}
  
